@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cardo:ital@1&family=Oswald:wght@600&display=swap);
*, li {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

body {
    font-family: 'Cardo', serif;
}

h1{
    font-family: 'Oswald', sans-serif;
    text-transform: uppercase;
}



.container {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    background-image: url('https://images.wallpaperscraft.com/image/mountains_sky_evening_138446_1920x1080.jpg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
}

.container__inner{
    background: inherit;
    width: 380px;
    height: 450px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -190px;
    margin-top: -225px;
    overflow: hidden;
    border-radius: 10px;
}

.container__inner:before{
    content: "";
    width: 430px;
    height: 500px; 
    background: inherit; 
    position: absolute;
    left: -25px;
    right: 0;
    top: -25px; 
    bottom: 0;
    box-shadow: inset 0 0 0 500px rgba(255,255,255,0.1);
    -webkit-filter: blur(10px);
            filter: blur(10px);
}

.caption{
    position: absolute;
    width: 100%;
    text-align: center;
    color: #ffce00;
}

.todo-form{
    position: absolute;
    width: 100%;
    text-align: center;
    margin-top: 15%;
}

.submit__button {
    cursor: pointer;
    position: absolute;
    width: 40px;
    height: 40px;
    right: 30px;
    top: 15px;
}


.task__input {
  background: 0;
  width: 220px;
  outline: 0;
  border: 0;
  border-bottom: 2px solid rgba(255,255,255, 0.3);
  margin: 20px 0;
  padding-bottom: 10px;
  font-size: 18px;
  font-weight: bold;
  color: rgba(255,255,255, 0.8);
}
li {
    margin-top: 2%;
}

.li__content {
    position: relative;
    margin: 10px;
}

.checkbox__label {
    position: absolute;
    left: 15%;
}

.checkbox {
    display: none;
}

.checkbox__fake {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 2px solid rgba(255, 255, 255, 0.289);
    border-radius: 4px;
    position: relative;
}

.checkbox__fake::before{
    content: "";
    position: absolute;
    display: block;
    width: 16px;
    height: 16px;
    background: url(/react-redux-todo/static/media/checkmark.c488cd24.svg) no-repeat;
    background-size: 16px 16px;
    opacity: 0;
    transition: .2s;
}

.checkbox:checked + .checkbox__fake::before {
    opacity: 1;
}

.task {
    display: inline-block;
    max-width: 150px;
    text-align: left;
    word-wrap: break-word;
    font-size: 17px;
    color: bisque;
}

.delete-btn {
    position: absolute;
    right: 10%;
    height: 25px;
    width: 25px;
    color: red;
    cursor: pointer;
}


.confirm-edit-btn {
    position: absolute;
    cursor: pointer;
    color: rgb(13, 225, 84);
    height: 25px;
    width: 25px;
    right: 12%; 
}

.change__input {
    text-align: center;
    background: 0;
    width: 220px;
    outline: 0;
    border: 0;
    border-bottom: 2px solid rgba(255,255,255, 0.3);
    padding-bottom: 10px;
    font-size: 15px;
    color: rgba(255,255,255, 0.8);
}

.escape-edit-btn {
    position: absolute;
    height: 25px;
    width: 25px;
    right: 5%;
    cursor: pointer;
}

.change_li {
    margin: 3%;
}
.wrapper {
    height: 230px;
    overflow: auto;
}

.wrapper::-webkit-scrollbar {
    width: 12px;
}

.wrapper::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
    border-radius: 10px;
}

.wrapper::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}

ul {
    height: 100%;
}

.counter{
    display: block;
    position: absolute;
    bottom: 25%;
}

.submit_button {    
    margin-left: 1px;
    margin-right: 1px;
    width: 30%;
    border: 0;
    border-radius: 8px;
    padding-bottom: 0;
    height: 45px;
    background: #df2359;
    color: white;
    cursor: pointer;
    transition: all 600ms ease-in-out;
}

.submit_button:hover{
    background: #C0392B;
}
.submit_button:active {
    outline: none;
}

.submit_button:focus {
    outline: none;
}

.buttons__container {
    width: 100%;
    text-align: center;
    position: absolute;
    bottom: 5%;
}

